<template lang="html">
<div id="index">
    <img class="logo" src="../assets/header.png" alt="">
    <mt-navbar v-model="selected">
        <mt-tab-item id="1">农村服务</mt-tab-item>
        <mt-tab-item id="2">城市服务</mt-tab-item>
        <mt-tab-item id="3" v-if="isImportant">重点系统</mt-tab-item>
    </mt-navbar>

    <!-- tab-container -->
    <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
            <div class="content-padding">
                <tab-item :dataList="vliData" :type="1" @DateReturn="getDateReturnHandle"></tab-item>
            </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
            <div class="content-padding">
                <tab-item :isVillage="false" :type="2" :dataList="cityData" @DateReturn="getDateReturnHandle"></tab-item>
            </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="3" v-if="isImportant">
            <div class="content-padding">
                <tab-project :dataList="projectData" :weekStr="weekStr"></tab-project>
            </div>
        </mt-tab-container-item>
    </mt-tab-container>
</div>
</template>

<script>
import TabItem from '../components/tab-item2';
import TabProject from '../components/tab-project';
import Vue from 'vue';

export default {
    data() {
        return {
            selected: '1',
            vliData: {},
            cityData: {},
            projectData: [],
            weekStr: 1
        }
    },
    components: {
        [TabItem.name]: TabItem,
        [TabProject.name]: TabProject,
    },
    computed: {
        isImportant() {
            return this.projectData.length > 0
        },
    },
    methods: {
        async getDateReturnHandle(obj) {
            try {
                let response = {};
                if (obj.type === 1) {
                    let response = await Vue.axios.get('/country/index');
                    console.log('response', response)
                    this.vliData = response;
                    response = await Vue.axios.get(`/country/total?begTime=${obj.start}&endTime=${obj.end}`);
                    let resultData = Object.assign({}, this.vliData, {
                        totalItem: response.totalItem,
                        totalSale: response.totalSale,
                        totalStages: response.totalStages,
                        arbitrarilyPie: response.arbitrarilyPie,
                        stagesPie: response.stagesPie,
                    });
                    this.vliData = resultData;
                    console.log('vliData', this.vliData)
                } else {
                    let response = await Vue.axios.get('/city/index');
                    this.cityData = response;
                    response = await Vue.axios.get(`/city/total?begTime=${obj.start}&endTime=${obj.end}`);
                    let resultData = Object.assign({}, this.cityData, {
                        totalItem: response.totalItem,
                        totalSale: response.totalSale,
                    });
                    this.cityData = resultData;
                }
            } catch (e) {
                console.log(e);
            }
        },
        async getVliData() {
            try {
                let response = await Vue.axios.get('/country/index');
                this.vliData = response;
            } catch (e) {
                console.log(e);
            }
        },
        async getCityData() {
            try {
                let response = await Vue.axios.get('/city/index');
                this.cityData = response;
            } catch (e) {
                console.log(e);
            }
        },
        async getProjectData() {
            try {
                let response = await Vue.axios.get('/project/index');
                this.projectData = response.data ? response.data.map((item) => {
                    return Object.assign({}, item, {
                        progress: item.progress > 100 ? 100 : item.progress,
                    })
                }) : [];
            } catch (e) {
                console.log(e);
            }
        },
        async getTime() {
            try {
                let response = await Vue.axios.get('/getTime');
                let time = response
                let week = new Date(time).getDay()
                let hour = new Date(time).getHours()
                let temp = 1
                if(week == 0 || week == 6) {
                    temp = 2
                }
                if(week == 5 && hour > 15){
                    temp = 2
                }
                this.weekStr = temp
            } catch (e) {
                console.log(e);
            }
        },

    },
    mounted() {
        this.getVliData();
        this.getCityData();
        this.getProjectData();
        this.getTime();
    },
}
</script>

<style lang="less">
#index {
    .logo {
        width: 100%;
    }
    .mint-navbar {
        .bar {
            width: 2px;
            height: 1.8rem;
            margin: 0.8rem 0;
            background-color: #ddd;
        }
        .mint-tab-item {
            padding: 1.1rem 0;
            border-bottom: 1px solid #ddd;
            color: #a5a5a5;
            .mint-tab-item-label {
                font-size: 1.1rem;
            }
            &.is-selected {
                border-bottom: 3px solid #E84740;
                color: #E84740;
                margin-bottom: 0;
                font-weight: 600;
            }
        }
    }
    .mint-tab-container {
        .content-padding {
            padding: 0 1rem 3rem 1rem;
        }
    }
}
</style>



