<template>
<div id="questionnaire-index">
    <header>返回</header>
    <h2 class="tc">国资商城&nbsp;问卷调查</h2>
    <div class="index-main-img">
        <img src="../../assets/questionnaire/images/readCard-img.png" />
    </div>
    <p class="index-readCard-msg tc">
        请把<span class="t-read">智慧生活卡</span>靠近读卡区
    </p>
    <p class="questionnaire-page-btn tc t-read" @click="openUserInfoPage">无卡请点击这里</p>
</div>
</template>

<script>

import Vue from 'vue';

export default {
    data() {
        return {

        }
    },
    components: {

    },
    computed: {

    },
    methods: {
        openUserInfoPage() {
            this.$router.push('/questionnaireUserInfo');
        },
        async getVliData() {
            try {
                let response = await Vue.axios.get('/country/index');
                this.vliData = response;
            } catch (e) {
                console.log(e);
            }
        },
    },
    mounted() {

    },
}
</script>

<style lang="less">
#questionnaire-index {
    header {
        height: 40px;
        padding-left: 20px;
        font-size: 16px;
        display: flex;
        align-items: center;
    }
    h2 {
        margin: 40px 0;
    }
    .index-main-img {
        width: 220px;
        margin: 0 auto;
        img {
            max-width: 100%;
        }
    }
    .index-readCard-msg {
        font-size: 16px;
        margin: 30px  0 50px 0;
    }
}
</style>



