<template lang="html">
    <div class="page-section">
        <div class="page-section-title">
            <p class="page-section-mainTitle">{{title}}</p>
            <p class="page-section-subTitle">{{subTitle}}</p>
        </div>
        <div class="page-section-content">
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
    name: 'page-section',
    props: {
        title: {
            type: String,
            default: '',
        },
        subTitle: {
            type: String,
            default: '',
        },
    },
    methods: {

    },
};
</script>

<style lang="less" scoped>
    .page-section {
        // counter-reset: titleNo;
        .page-section-title, .page-section-content {
            padding: 8px 15px;
        }
        .page-section-title {
            background: #eee;
            color: #333;
        }
        .page-section-mainTitle:before {
            counter-increment: titleNo;
            content: counter(titleNo) "、";
        }
        .page-section-subTitle {
            font-size: 11px;
            color: #bbb;
        }
    }
</style>



