<template lang="html">
    <div class="page-wrap">
        <question-header>
        </question-header>
        <div class="page-main">
            <slot></slot>
        </div>
    </div>
</template>

<script>
import QuestionHeader from '../../components/questionnaire/Header';

export default {
    name: 'layout',
    components: {
        QuestionHeader,
    },
    props: {

    },
    methods: {

    },
};
</script>

<style lang="less">
    .page-main {
        margin-top: 45px;
    }
    .input-error-msg {
        color: red;
        margin: 5px;
    }
    input, select, textarea {
        font-size: 16px;
    }
    select {
        border: 1px solid #ccc;
        height: 30px;
    }
    input[type=radio] {
        display: none;
    }
    .custom-radio-innerIcon {
        display: inline-block;
        width: 18px;
        height: 18px;
        border-radius: 100%;
        border: 1px solid #ccc;
        vertical-align: middle;
        position: relative;
    }
    input[type=radio]:checked + .custom-radio-innerIcon {
        background: red;
        width: 20px;
        height: 20px;
        border: none;
    }
    input[type=radio]:checked + .custom-radio-innerIcon:after {
        content: '';
        display: inline-block;
        width: 10px;
        height: 10px;
        border-radius: 100%;
        background: #fff;
        position: absolute;
        top: 5px;
        left: 5px;
    }
    input[type=checkbox] {
        display: none;
    }
    .custom-checkbox-innerIcon {
        display: inline-block;
        width: 18px;
        height: 18px;
        border: 1px solid #ccc;
        border-radius: 100%;
        vertical-align: middle;
        position: relative;
    }
    input[type=checkbox]:checked + .custom-checkbox-innerIcon {
        background: red;
        width: 20px;
        height: 20px;
        border: none;
    }
    input[type=checkbox]:checked + .custom-checkbox-innerIcon:after {
        content: '';
        display: inline-block;
        width: 8px;
        height: 4px;
        border-color: #fff !important;
        border-left: 2px solid;
        border-bottom: 2px solid;
        transform: rotate(-45deg);
        position: absolute;
        top: 6px;
        left: 5px;
    }
</style>



