<script>

import Vue from 'vue';
import Layout from '../../components/questionnaire/Layout';
import PageSection from '../../components/questionnaire/PageSection';
import QuestionInput from '../../components/questionnaire/Input';

export default {
    data() {
        return {
            familyRelation: [
                {text: '父亲', value: '1'},
