                {text: '7年', value: '7'},
                {text: '8年', value: '8'},
                {text: '9年', value: '9'},
                {text: '10年以上', value: '10'},
            ],
            plantTypeList: [
                {text: '水稻', value: '水稻'},
                {text: '玉米', value: '玉米'},
                {text: '小麦', value: '小麦'},
                {text: '蔬菜', value: '蔬菜'},
                {text: '水果', value: '水果'},
                {text: '茶叶', value: '茶叶'},
                {text: '马铃薯', value: '马铃薯'},
                {text: '鲜花', value: '鲜花'},
                {text: '甘蔗', value: '甘蔗'},
                {text: '橡胶', value: '橡胶'},
                {text: '咖啡', value: '咖啡'},
                {text: '其他（请填写）', value: '0'},
            ],
            cultivateTypeList: [
                {text: '牛', value: '牛'},
                {text: '羊', value: '羊'},
                {text: '猪', value: '猪'},
                {text: '鸡鸭', value: '鸡鸭'},
                {text: '鱼', value: '鱼'},
                {text: '其他（请填写）', value: '0'},
            ],

            familyPlantingsModel: {
                plantCropsName: '',
                plantAreaCount: '',
                otherPlantCropsName: '',
                otherPlantAreaCount: '',
            },
            familyCulturesModel: {
                cultureType: '',
                annualYield: '',
                annualYieldUnit: '只',
                otherCultureType: '',
                otherAnnualYield: '',
            },
            addFamilyPersonError: false,
            addPlantError: false,
            addCultureError: false,
            note: '',
            validatedData: {
                holderEducation: '',
                personCount: '',
                holderDomicile: '',
                holderLive: '',
                liveYear: '',
                lastYearFamilyIncome: '',
                incomeSource: [],
                incomeSourceOther: '',
                fieldSize: 0,
                landSize: 0,
                forestLandSize: 0,
                mountainLandSize: 0,
                familyMembers: [],
                familyMembersModel: [],
                familyPlantings: [],
                familyCultures: [],
                carFlag: '',
                carNo: '',
                lastYearFamilyExpense: '',
                expenseSource: [],
                monthDailySuppliesRange: '',
                monthDailyChemicalsRange: '',
                otherLandSize: 0,
                expenseSourceOther: '',
                destituteFlag: '',
            },
            cityList: [],
            censusRegisternCountyList: [],
            censusRegisternTownList: [],
            censusRegisternVillageList: [],
            lifePlaceCountyList: [],
            lifePlaceTownList: [],
            lifePlaceVillageList: [],
            censusRegisternCityModel: 0,
            censusRegisternCountyModel: 0,
            censusRegisternTownModel: 0,
            lifePlaceCityModel: 0,
            lifePlaceCountyModel: 0,
            lifePlaceTownModel: 0,
            lifeAndcensusIsSame: '1',
            validateConfig: {
                holderEducation: {
                    vType: ['require'],
                    errorMsg: '请选择学历',
                },
                personCount: {
                    vType: ['require'],
                    errorMsg: '请填写正确的家庭总人口数',
                },
                familyMembers: {
                    vType: ['requireFamilyMembers'],
                    errorMsg: '请添加家庭人员信息',
                },
                liveYear: {
                    vType: ['require'],
                    errorMsg: '请选择居住时长',
                },
                lastYearFamilyIncome: {
                    vType: ['require'],
                    errorMsg: '请选择家庭总收入',
                },
                incomeSource: {
                    vType: ['require'],
                    errorMsg: '请选择家庭收入来源',
                },
                incomeSourceOther: {
                    vType: ['requireIncomeSourceOther'],
                    errorMsg: '请填写其他收入来源',
                },
                landSize: {
                    vType: ['isNumber'],
                    errorMsg: '请输入正确的数字',
                },
                fieldSize: {
                    vType: ['isNumber'],
                    errorMsg: '请输入正确的数字',
                },
                forestLandSize: {
                    vType: ['isNumber'],
                    errorMsg: '请输入正确的数字',
                },
                mountainLandSize: {
                    vType: ['isNumber'],
                    errorMsg: '请输入正确的数字',
                },
                familyPlantings: {
                    vType: ['require'],
                    errorMsg: '请添加家庭种植信息',
                },
                familyCultures: {
                    vType: ['require'],
                    errorMsg: '请添加家庭养植信息',
                },
                lastYearFamilyExpense: {
                    vType: ['require'],
                    errorMsg: '请选择家庭支出',
                },
                expenseSource: {
                    vType: ['require'],
                    errorMsg: '请选择家庭支出用于',
                },
                expenseSourceOther: {
                    vType: ['requireExpenseSourceOther'],
                    errorMsg: '请填写其他支出',
                },
                monthDailySuppliesRange: {
                    vType: ['require'],
                    errorMsg: '请选择月平均支出',
                },
                monthDailyChemicalsRange: {
                    vType: ['require'],
                    errorMsg: '请选择月平均支出',
                },
                carFlag: {
                    vType: ['require'],
                    errorMsg: '请选择是否有汽车',
                },
                carNo: {
                    vType: ['requireCarNo'],
                    errorMsg: '请填写正确的汽车数量',
                },
                otherLandSize: {
                    vType: ['isNumber'],
                    errorMsg: '请输入正确的数字',
                },
                destituteFlag: {
                    vType: ['require'],
                    errorMsg: '请选择是否是建档立卡户',
                },
            },
            validateError: {

            },
            validateType: {
                require: {
                    validate(val) {
                        if (Array.isArray(val)) {
                            if (val.length === 0) {
                                return false;
                            }
                        } else {
                            let targetVal = String(val).replace(/^\s*\s*$/, '');
                            if (targetVal.length === 0) return false;
                        }
                        return true;
                    },
                },
                isNumber: {
                    validate(val) {
                        if (isNaN(val) || !/^(0|[1-9][0-9]*)(\.[0-9]+)?$/.test(val)) {
                            return false;
                        }
                        return true;
                    },
                },
                positiveInteger: {
                    validate(val) {
                        if (isNaN(val) || !/^[1-9][0-9]*$/.test(val)) {
                            return false;
                        }
                        return true;
                    },
                },
                requireIncomeSourceOther: {
                    validate(val) {
                        if (this.validatedData.incomeSource.indexOf('其他') >=0 && !val) {
                            return false;
                        }
                        return true;
                    },
                },
                requireExpenseSourceOther: {
                    validate(val) {
                        if (this.validatedData.expenseSource.indexOf('其他') >=0 && !val) {
                            return false;
                        }
                        return true;
                    },
                },
                requireFamilyMembers: {
                    validate(val) {
                        let hasError = false;
                        // if (val.length === 0) return false;
                        val.forEach(item => {
                            Object.keys(item).forEach(key => {
                                if (String(item[key]).length === 0) {
                                    hasError = true;
                                    return false;
                                }
                            });
                            if (hasError) return false;
                        });
                        return !hasError;
                    },
                },
                requireCarNo: {
                    validate(val) {
                        if (this.validatedData.carFlag === '1' && !/^[1-9][0-9]*$/.test(val)) {
                            return false;
                        }
                        return true;
                    },
                },
            },
            validator: {
                handleValidate(field) {
                    const targetField = this.validateConfig[field],
                          targetValue = this.validatedData[field];
                    const hasError = targetField.vType.some(vType => (
                        !this.validateType[vType].validate.bind(this)(targetValue)
                    ));
                    if (hasError) {
                        this.$set(this.validateError, field, targetField.errorMsg);
                    } else {
                        delete this.validateError[field];
                    }
                },
                validate() {
                    this.validateError = {};
                    Object.keys(this.validateConfig).forEach(key => {
                        this.validator.handleValidate.bind(this)(key);
                    });
                },
                validateField(field) {
                    this.handleValidate(field);
                },
            },
        };
    },
    components: {
        Layout,
        PageSection,
        QuestionInput,
    },
    watch: {
        censusRegisternCityModel(val) {
            if (this.lifeAndcensusIsSame === '0') return;
            this.$set(this, 'lifePlaceCityModel', val);
            this.changeLifePlaceCounty();
        },
        censusRegisternCountyModel(val) {
            if (this.lifeAndcensusIsSame === '0') return;
            this.$set(this, 'lifePlaceCountyModel', val);
            this.changeLifePlaceTown();
        },
        censusRegisternTownModel(val) {
            if (this.lifeAndcensusIsSame === '0') return;
            this.$set(this, 'lifePlaceTownModel', val);
            this.changeLifePlaceVillage();
        },
        'validatedData.holderDomicile'(val) {
            if (this.lifeAndcensusIsSame === '0') return;
            this.$set(this.validatedData, 'holderLive', val);
        },
        'validatedData.personCount'(val) {
            this.validatedData.familyMembers.length = 0;
            for (var i = 0; i < val - 1; i++) {
                const targetItem = {
                    holderRelation: '',
                    ageRange : '',
                    occupationType: '',
                };
                this.validatedData.familyMembers.push(targetItem);
            }
        },
        validatedData: {
            handler(val, param, param2) {
                this.validator.validate.bind(this)();
            },
            deep: true,
        },
    },
    computed: {
        holderName() {
            return this.$route.query.holderName;
        },
        holderIdNo() {
            return this.$route.query.holderIdNo;
        },
        holderMobile() {
            return this.$route.query.holderIdNo;
        },
        cardNo() {
            return this.$route.query.card_id;
        },
        areaId() {
            const areaIdArray = this.$route.query.area_id.split(',');
            if (areaIdArray.length === 0) return;
            areaIdArray[1] ? this.$set(this, 'censusRegisternCityModel', areaIdArray[1]) : '';
            areaIdArray[2] ? this.$set(this, 'censusRegisternCountyModel', areaIdArray[2]) : '';
            areaIdArray[3] ? this.$set(this, 'censusRegisternTownModel', areaIdArray[3]) : '';
            areaIdArray[4] ? this.$set(this.validatedData, 'holderDomicile', areaIdArray[4]) : '';
            this.changeCensusRegisternCounty();
            this.changeCensusRegisternTown();
            this.changeCensusRegisternVillage();
        },
        isShowSubmitError() {
            return Object.keys(this.validateError).length;
        },
    },

    methods: {

        getRegionList(level, parentSysNo) {
            return Vue.axios.post(`/eccapi/api/ApiCenter/Gzzs/GetRegionList?level=${level}&parentSysNo=${parentSysNo}`);
        },
        addFamilyPersonItem() {
            const holderRelation = this.familyMembersModel.holderRelation,
                  rangeAge = this.familyMembersModel.ageRange,
                  occupationType = this.familyMembersModel.occupationType;
            if (!holderRelation || !rangeAge || !occupationType) {
                this.$set(this, 'addFamilyPersonError', true);
                return;
            }
            const targetItem = {
                holderRelation,
                'holderRelationText': this.familyRelation.find(item => item.value === holderRelation).text,
                rangeAge,
                'rangeAgeText': this.ageList.find(item => item.value === rangeAge).text,
                occupationType,
                'occupationTypeText': this.positionList.find(item => item.value === occupationType).text,
            };
            this.validatedData.familyMembers.push(targetItem);
            this.$set(this, 'addFamilyPersonError', false);
        },
        deleteFamilyPersonItem(item) {
            const index = this.validatedData.familyMembers.indexOf(item);
            this.validatedData.familyMembers.splice(index, 1);
        },
        addPlantItem() {
            let plantCropsName = this.familyPlantingsModel.plantCropsName,
                plantAreaCount = this.familyPlantingsModel.plantAreaCount;
            if (plantCropsName === '0') {
                plantCropsName = this.familyPlantingsModel.otherPlantCropsName;
                plantAreaCount = this.familyPlantingsModel.otherPlantAreaCount;
            }
            if (!plantCropsName
                || !plantAreaCount
                || !this.validateType.isNumber.validate(plantAreaCount)) {
                this.$set(this, 'addPlantError', true);
                return;
            }
            const targetItem = {
                plantCropsName,
                plantAreaCount,
            };
            this.validatedData.familyPlantings.push(targetItem);
            this.$set(this, 'addPlantError', false);
            this.familyPlantingsModel.plantCropsName = '';
            this.familyPlantingsModel.plantAreaCount = '';
            this.familyPlantingsModel.otherPlantCropsName = '';
            this.familyPlantingsModel.otherPlantAreaCount = '';
        },
        deletePlantItem(item) {
            const index = this.validatedData.familyPlantings.indexOf(item);
            this.validatedData.familyPlantings.splice(index, 1);
        },
        addCultivateItem() {
            let cultureType = this.familyCulturesModel.cultureType,
                annualYield = this.familyCulturesModel.annualYield;
            if (cultureType === '0') {
                cultureType = this.familyCulturesModel.otherCultureType;
                annualYield = this.familyCulturesModel.otherAnnualYield;
            }
            if (!cultureType
                || !annualYield
                || !this.validateType.positiveInteger.validate(annualYield)) {
                this.$set(this, 'addCultureError', true);
                return;
            }
            const targetItem = {
                cultureType,
                annualYield,
                annualYieldUnit: (cultureType === '鱼' ? '公斤' : '只'),
            };
            this.validatedData.familyCultures.push(targetItem);
            this.$set(this, 'addCultureError', false);
            this.familyCulturesModel.cultureType = '';
            this.familyCulturesModel.otherCultureType = '';
            this.$set(this.familyCulturesModel, 'annualYield', '');
            this.familyCulturesModel.otherAnnualYield = '';
        },
        deleteCultivateItem(item) {
            const index = this.validatedData.familyCultures.indexOf(item);
            this.validatedData.familyCultures.splice(index, 1);
        },
        validateRequire() {
            this.validateError = {};
            Object.keys(this.validatedData).forEach(key => {
                if (Array.isArray(this.validatedData[key])) {
                    if (this.validatedData[key].length === 0) {
                        this.validateError[key] = true;
                    }
                } else if (!this.validatedData[key]) {
                    this.validateError[key] = true;
                }
            });
        },
        setCityList(data) {
            this.$set(this, 'cityList', data);
        },
        changeCensusRegisternCounty() {
            this.getRegionList(2, this.censusRegisternCityModel).then(result => {
                this.$set(this, 'censusRegisternCountyList', result.ResultData);
            });
        },
        changeCensusRegisternTown() {
            this.getRegionList(3, this.censusRegisternCountyModel).then(result => {
                this.$set(this, 'censusRegisternTownList', result.ResultData);
            });
        },
        changeCensusRegisternVillage() {
            this.getRegionList(4, this.censusRegisternTownModel).then(result => {
                this.$set(this, 'censusRegisternVillageList', result.ResultData);
            });
        },
        changeLifePlaceCounty() {
            this.getRegionList(2, this.lifePlaceCityModel).then(result => {
                this.$set(this, 'lifePlaceCountyList', result.ResultData);
            });
        },
        changeLifePlaceTown() {
            this.getRegionList(3, this.lifePlaceCountyModel).then(result => {
                this.$set(this, 'lifePlaceTownList', result.ResultData);
            });
        },
        changeLifePlaceVillage() {
            this.getRegionList(4, this.lifePlaceTownModel).then(result => {
                this.$set(this, 'lifePlaceVillageList', result.ResultData);
            });
        },
        combinationSubmitData() {
            return {
                familyDocument: {
                    holderName: this.holderName || '',
                    holderIdNo: this.holderIdNo || '',
                    holderMobile: this.holderMobile || '',
                    cardNo: this.cardNo || '',
                    holderEducation: this.validatedData.holderEducation,
                    personCount: this.validatedData.personCount,
                    holderDomicile: this.validatedData.holderDomicile,
                    holderLive: this.validatedData.holderLive,
                    liveYear: this.validatedData.liveYear,
                    lastYearFamilyIncome: this.validatedData.lastYearFamilyIncome,
                    incomeSource: this.validatedData.incomeSource.join(',').replace('其他', ''),
                    incomeSourceOther: this.validatedData.incomeSourceOther,
                    fieldSize: this.validatedData.fieldSize,
                    landSize: this.validatedData.landSize,
                    forestLandSize: this.validatedData.forestLandSize,
                    mountainLandSize: this.validatedData.mountainLandSize,
                    note: this.note,
                    lastYearFamilyExpense: this.validatedData.lastYearFamilyExpense,
                    expenseSource: this.validatedData.expenseSource.join(',').replace('其他', '') + this.validatedData.expenseSourceOther,
                    carFlag: this.validatedData.carFlag,
                    carNo: this.validatedData.carNo,
                    monthDailySuppliesRange: this.validatedData.monthDailySuppliesRange,
                    monthDailyChemicalsRange: this.validatedData.monthDailyChemicalsRange,
                    otherLandSize: this.validatedData.otherLandSize,
                    destituteFlag: this.validatedData.destituteFlag,

                },
                familyMembers: this.validatedData.familyMembers,
                familyPlantings: this.validatedData.familyPlantings,
                familyCultures: this.validatedData.familyCultures,
            };
        },
        submit() {
            const data = this.combinationSubmitData();
            this.$set(this, 'addPlantError', false);
            this.$set(this, 'addCultureError', false);
            this.validator.validate.bind(this)();
            if (Object.keys(this.validateError).length > 0) return;
            Vue.axios.post('/villagerecordapi/api/questionnaire', data)
            .then(result => {
                alert(result.message);
                console.log(result);
            });
        },
    },
    mounted() {
        this.getRegionList(1, 1).then(result => {
            this.setCityList(result.ResultData);
        });
    },
}
</script>

<style lang="less">
#questionnaire-Info {
    counter-reset: titleNo;
    .section-row {
        margin-bottom: 8px;
        input {
            vertical-align: top;
        }
    }
    .content-land {
        .section-row {
            margin-bottom: 10px;
        }
    }
    select {
        width: 130px;
    }
    .section-addItem-list {
        li {
            display: flex;
            margin-bottom: 8px;
            align-items: center;
            & > * {
                flex: 1;
            }
            .delete-item-btn {
                display: inline-block;
                padding: 2px 7px;
                background: #eee;
                border-radius: 5px;
            }
        }
    }
    .section-btn-wrap {
        text-align: right;
        margin: 20px 0;
    }
    .section-btn {
        display: inline-block;
        border: 1px solid;
        border-radius: 25px;
        width: 120px;
        padding: 5px 0;
        text-align: center;
    }
    .edit-censusRegistern-msg {
        color: #ccc;
    }
    .content-land {
        .input-wrap {
            margin: 0 8px;
        }
    }
    .section-row-flex {
        display: flex;
        & > * {
            flex: 1;
        }
    }
    .content-plant, .content-cultivate {
        select {
            width: 104px;
        }
        .input-wrap-outer {
            margin: 0 8px 0 15px;
        }
        .input-wrap:first-child {
            margin-left: 0;
        }
    }
    .textarea-suggest {
        width: 100%;
        height: 120px;
        border: 1px solid #ccc;
    }
    .questionnaire-btn-submit {
        margin: 10px auto;
    }

    .section-row-select {
        select {
            margin-left: 10px;
        }
        select:first-child {
            margin-left: 0;
        }
    }
    .input-wrap-outer {
        display: inline-block;
        vertical-align: middle;
    }
    .questionnaire-page-footer {
        padding: 30px 0 60px;
    }

}
</style>



