<template>
    <layout>
        <div id="questionnaire-userInfo">
            <page-section
                title="1、您的姓名">
                <question-input
                    v-model="formModel.holderName"
                    placeholder="请填写"
                    width="250px">
                </question-input>
            </page-section>
            <page-section
                title="2、您的身份证号">
                <question-input
                    v-model="formModel.holderIdNo"
                    placeholder="请填写"
                    width="250px">
                </question-input>
            </page-section>
            <page-section
                title="3、您的手机号">
                <question-input
                    v-model="formModel.holderMobile"
                    placeholder="请填写"
                    width="250px">
                </question-input>
            </page-section>
            <p class="questionnaire-page-btn tc t-read" @click="openQuestionnaireInfoPage">下一步</p>
        </div>
    </layout>

</template>

<script>

import Vue from 'vue';
import Layout from '../../components/questionnaire/Layout';
import PageSection from '../../components/questionnaire/PageSection';
import QuestionInput from '../../components/questionnaire/Input';

export default {
    data() {
        return {
            formModel: {
                holderName: '',
                holderIdNo: '',
                holderMobile: '',
            }
        }
    },
    components: {
        Layout,
        PageSection,
        QuestionInput,
    },
    computed: {

    },
    methods: {
        openQuestionnaireInfoPage() {
            this.$router.push({
                path: '/questionnaireInfo',
                query: {
                    holderName: this.formModel.holderName,
                    holderIdNo: this.formModel.holderIdNo,
                    holderMobile: this.formModel.holderMobile,
                }
            });
        },
    },
    mounted() {

    },
}
</script>

<style lang="less">
#questionnaire-userInfo {
    .page-section-content {
        padding-left: 35px;
        padding-bottom: 20px;
    }
    .questionnaire-page-btn {
        margin-top: 30px;
    }
}
</style>



