// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css'
import Axios from 'axios';
import VueAxios from 'vue-axios';

import echarts from 'echarts'
Vue.prototype.$echarts = echarts

import * as directives from './directives'

const axios = Axios.create({
    baseURL: '',
    timeout: 15000,
    validateStatus: () => true,
    responseType: 'json',
});
Vue.use(VueAxios, axios);
// 添加请求拦截器--请求之前做的事
Vue.axios.interceptors.request.use((config) => Object.assign({}, config, {
    headers: {
        // 'Content-Type': 'application/json',
    }
}));
// 添加响应拦截器
Vue.axios.interceptors.response.use((res) => {
    if (res.status === 200) {
        return res.data;
    }
    if (res.status === 500) {
        return {
            code: '500',
            msg: '服务器错误！'
        }
    }
    if (res.status >= 400) {
        return {
            code: '400',
            msg: '暂时无法访问，请稍后重试!'
        }
    }
}, (err) => {
    if (String(err).indexOf('Network Error') !== -1) {
        return {
            code: '100',
            msg: '网络错误！'
        }
    }
    return {
        code: '504',
        msg: '网络连接超时！'
    }
})
Vue.config.productionTip = false
Vue.use(Mint);

// directives
Object.keys(directives.default).forEach((name) => {
    const config = directives.default[name]
    Vue.directive(config.name, config.directive)
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    template: '<App/>',
    components: {
        App
    }
})



