import echarts from 'echarts';
let initChartArr = [];

export default {
  name: 'chart',
  directive: {
    update(el, binding, vnode) {
      if (!Object.is(binding.value, binding.oldValue) && binding.value) {
        if (Object.keys(binding.value).length > 0) {
          if (initChartArr[el.eid]) {
            initChartArr[el.eid].setOption(binding.value);
          } else {
            let initChart = echarts.init(el);
            initChartArr[initChart.id] = initChart;
            el['eid'] = initChart.id;
            initChart.setOption(binding.value);
          }
        }
      }
    },
    inserted(el, binding, vnode) {
      if (binding.value) {
        if (Object.keys(binding.value).length > 0) {
          let initChart = echarts.init(el);
          initChartArr[initChart.id] = initChart;
          el['eid'] = initChart.id;
          initChart.setOption(binding.value);
        }
      }
    }
  }
};



