</template>
<script>
import base from 'src/components/base';
export default {
    name: 'tab-item',
    props: {
        dataList: {
            type: Object,
            default: () => {}
        },
        isVillage: {
            type: Boolean,
            default: () => false
        },
        type: {
            type: Number,
            default: () => 1
        }
    },
    data() {
        return {
            pickerStart: '2016-10-17',
            pickerEnd: ''
        }
    },
    computed: {
        startDate() {
            return base.formatDate(this.pickerStart);
        },
        endDate() {
            return base.formatDate(this.pickerEnd);
        },
        startDateTa() {
            return new Date(base.formatDate(this.pickerStart));
        },
        endDateTa() {
            return new Date(base.formatDate(this.pickerEnd));
        },
        stagesOption() {
            return {
                title : {
                    text: '分期购充值档位占比',
                    x: 'center'
                },
                tooltip : {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                series : [
                    {
                        name: '分期购充值档位',
                        type: 'pie',
                        radius : '66%',
                        center: ['50%', '60%'],
                        data: this.dataList.stagesPie || [],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        },
                        labelLine: {
                            normal: {
                                length: 8,
                                length2: 6
                            }
                        },
                    }
                ]
            };
        },
        arbitrarilyOption() {
            return {
                title : {
                    text: '任意购充值档位占比',
                    x: 'center'
                },
                tooltip : {
                    trigger: 'item',
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                series : [
                    {
                        name: '任意购充值档位',
                        type: 'pie',
                        radius : '66%',
                        center: ['50%', '60%'],
                        data: this.dataList.arbitrarilyPie || [],
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        },
                        labelLine: {
                            normal: {
                                length: 8,
                                length2: 6
                            }
                        },
                    }
                ]
            };
        }
    },
    watch: {
        dataList(val, oldVal) {
            console.log(val);
        },
    },
    methods: {
        openPickerStart() {
            this.$refs.pickerStart.open();
        },
        openPickerEnd() {
            this.$refs.pickerEnd.open();
        },
        handleConfirm() {
            let obj = {
                start: base.formatDate(this.pickerStart),
                end: base.formatDate(this.pickerEnd),
                type: this.type,
            };
            this.$emit('DateReturn', obj)
        }
    },
    mounted() {
        let d = new Date();
        this.pickerEnd = base.formatDate(d.getFullYear() + '/' + (d.getMonth() + 1) + '/' + d.getDate()); // 获取当日日期
    }
}
</script>
<style lang="less">
.tab-item {
    font-size: 1rem;
    .data-item {
        padding-top: 1rem;
        .header-box {
            line-height: 2;
            font-weight: 600;
            font-size: 1.1rem;
            padding: 0 0.5rem;
            .icon {
                width: 0.7rem;
                vertical-align: text-bottom;
                margin-bottom: 0.2rem;
            }
            .datepicker {
                float: right;
                display: flex;
                align-items: center;
                margin-top: 0.5rem;
                input.str {
                    width: 5rem;
                    font-size: 0.9rem;
                    color: #787878;
                    border: none;
                    padding-right: 0.8rem;
                    background: url('../assets/tri.png') no-repeat center right;
                    background-size: 0.8rem;
                }
                .line {
                    width: 1rem;
                    text-align: center;
                    height: 1px;
                    margin: 0 0.5rem;
                    background-color: #787878;
                }
            }
        }
        .content-box {
            .title {
                color: #666;
                line-height: 2.5;
                padding: 0 0.5rem;
            }
            .data-ul {
                color: #999;
                background-color: rgba(221, 221, 221, 0.4);
                padding: 1rem 1.2rem;
                border-radius: 6px;
                margin: 0.5rem 0;
                li {
                    display: flex;
                    align-items: flex-end;
                    padding: 7px 0;
                    .left{
                        /*width: 8.5rem;*/
                        text-align: right;
                        width: auto
                    }
                    .right {
                        flex: auto;
                        text-align: right;
                        em {
                            font-style: normal;
                            font-size: 1.2rem;
                            font-weight: 400;
                            color: #E73830;
                            padding-right: 0.8rem;
                        }
                    }
                    .charts {
                        width: 100%;
                        height: 300px;
                    }
                }
            }
        }
    }
    .mint-datetime-action {
        color: #E73830;
    }
}
</style>



