<template lang="html">
    <div class="input-wrap">
        <input
            :type="type"
            :placeholder="placeholder"
            :style="`width: ${width}`"
            :value="value"
            :min="min"
            :max="max"
            :maxlength="maxlength"
            @input="inputHandler" />
    </div>
</template>

<script>
export default {
    name: 'question-input',
    props: {
        placeholder: {
            type: String,
            default: '',
        },
        width: {
            type: String,
            default: '160px',
        },
        value: '',
        type: {
            type: String,
            default: 'text',
        },
        min: {
            type: String,
            default: '',
        },
        max: {
            type: String,
            default: '',
        },
        maxlength: {
            type: String,
            default: '',
        },
    },
    methods: {
        inputHandler(e) {
            this.$emit('input', e.target.value);
        }
    },
};
</script>

<style lang="less">
    .input-wrap {
        display: inline-block;
        vertical-align: middle;
        input {
            border: 1px solid #ccc;
            border-radius: none;
            height: 26px;
            line-height: 26px;
        }
    }
</style>



