<template lang="html">
    <div class="gz-header">
        <div class="gz-header-left" @click="leftHandler">
            <slot name="left-icon">
                <!-- <div class="header-return-icon"></div> -->
            </slot>
        </div>
        <div class="gz-header-title">{{title}}</div>
        <div class="gz-header-right" @click="rightHandler">
            <slot name="right-icon">
            </slot>
        </div>
        <slot>
        </slot>
    </div>
</template>

<script>
export default {
    name: 'question-header',
    props: {
        title: {
            type: String,
            default: '国资商城 问卷调查',
        }
    },
    methods: {
        leftHandler() {
            if (this.leftIcon) this.$emit('left-icon-click');
        },
        rightHandler() {
            if (this.rightIcon) this.$emit('right-icon-click');
        },
    },
};
</script>

<style lang="less">
    .gz-header {
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        text-align: center;
        height: 45px;
        background: red;
        color: #fff;
        font-size: 12px;
        z-index:99;
        -webkit-tap-highlight-color:rgba(255,255,255,0);
        .gz-header-right, .gz-header-left {
            padding: 0 5%;
            width: 30px;
        }
        .gz-header-title {
            flex: 1.0;
            font-size: 16px;
        }
        .header-return-icon {
            width: 13px;
            height: 22px;
            background: url(../../assets/questionnaire/images/arrow-left.png);
        }
    }
</style>



