<template lang="html">
<div class="tab-project">
    <div class="cell" @click="setShowKey(item.id)" v-for="(item,index) in dataList">
        <div class="header-box">
            <img class="icon" src="../assets/right-arrow.png" alt="">
            {{ index+1 < 10 ? '0' + (index+1).toString(): index+1 }}{{item.name}}
        </div>
        <div>本周进度</div>
        <mt-progress :value="item.progress" :bar-height="13" :class="{ fullProgress: item.progress >= 100 }">
            <div slot="end" class="value">{{item.progress}}%</div>
        </mt-progress>
        <transition name="fade">
            <div :ref="`content${item.id}`" class="content" v-show="showKey === item.id">
                <div class="content-cell" v-for="sub in item.content">
                    <h3 v-if="sub.title == '主要功能'">
                        <span>{{sub.title}}</span>
                        <div class="line"></div>
                    </h3>
                    <h3 v-if="sub.title == '本周总结'">
                        <span>{{ weekStr == 1 && sub.title == '本周总结' ? '上周总结' :sub.title}}</span>
                        <div class="line"></div>
                    </h3>
                    <h3 v-if="sub.title == '下周计划'">
                        <span>{{ weekStr == 1 && sub.title == '下周计划' ? '本周计划' :sub.title}}</span>
                        <div class="line"></div>
                    </h3>
                    <ul>
                        <li v-for="(val, index) in sub.content"><span>{{index + 1}}、</span><span>{{val}}</span></li>
                    </ul>
                </div>
            </div>
        </transition>
        <div class="bottom">
            <div class="icon">
                <img src="../assets/down-arrow.png" alt="" :class="{ rotate: showKey === item.id }">
            </div>
            <div class="line"></div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: 'tab-project',
    props: {
        dataList: {
            type: Array,
            default: () => []
        },
        weekStr: {
            type: Number,
            default: 1
        },
    },
    data() {
        return {
            showKey: null,
        }
    },
    methods: {
        setShowKey(id) {
            if (this.showKey === id) {
                this.showKey = null;
            } else {
                this.showKey = id;
            }
        }
    }
}
</script>

<style lang="less">
.tab-project {
    .cell {
        padding: 0.5rem;
        .header-box {
            font-weight: 600;
            font-size: 1.1rem;
            padding: 1rem 0;
            .icon {
                width: 0.9rem;
                vertical-align: text-bottom;
                margin-bottom: 0.2rem;
            }
        }
        .content {
            max-height: 1000px;
            position: relative;
            overflow-y: auto;
            .content-cell {
                padding: 0 0.5rem;
                font-size: 0.9rem;
                h3 {
                    position: relative;
                    padding: 0.8rem 0;
                    font-size: 1rem;
                    font-weight: 600;
                    color: #555;
                    span {
                        background-color: #fff;
                        padding: 0.5rem 1rem 0.5rem 0;
                    }
                    .line {
                        width: 100%;
                        height: 1px;
                        background-color: #e0e0e0;
                        position: absolute;
                        top: 50%;
                        left: 0;
                        z-index: -1;
                    }
                }
                ul {
                    color: #999;
                    font-weight: 400;
                    li {
                        display: flex;
                    }
                }

            }
        }
        .fade-enter-active, .fade-leave-active {
            transition: max-height .5s
        }
        .fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
            max-height: 0;
        }
        .value {
            padding: 0 0.5rem;
            width: 30px;
            font-size: 1rem;
            color: #787878;
            transition: height 0.6s;
        }
        .mt-progress {
            padding-bottom: 0.5rem;
            .mt-progress-progress {
                background: #e63e37 url('../assets/mask.png') no-repeat right top;
            }
            &.fullProgress {
                .mt-progress-progress {
                    background: #e63e37;
                }
            }
        }
        .bottom {
            width: 100%;
            position: relative;
            margin-top: 1.5rem;
            .icon {
                width: 1.6rem;
                margin: 0 auto;
                padding: 0 1.5rem;
                background-color: #fff;
                img {
                    width: 100%;
                    transition: transform .5s;
                    &.rotate {
                        transform: rotate(180deg);
                    }
                }
            }
            .line {
                width: 100%;
                height: 1px;
                background-color: #e2e2e2;
                position: absolute;
                top: 20%;
                left: 0;
                z-index: -1;
            }
        }
    }
}
</style>



