import Vue from 'vue'
import Router from 'vue-router'

// import Index from 'src/pages/index'
import IndexPro from 'src/pages/indexpro'
import IndexSP from 'src/pages/indexsp'
import Questionnaire from 'src/pages/questionnaire/index';
import QuestionnaireUserInfo from 'src/pages/questionnaire/userInfo';
import QuestionnaireInfo from 'src/pages/questionnaire/questionnaireInfo';

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/qgapZYsCR6lcdtTY',
        name: 'IndexSP',
        component: IndexSP
    }, {
        path: '/qgapZYsCR6lcdtTT',
        name: 'IndexPro',
        component: IndexPro
    }, {
        path: '/qgapZYsCR6lcdtTZ',
        name: 'IndexSP',
        component: IndexSP
    }, {
        path: '/questionnaire',
        component: Questionnaire
    }, {
        path: '/questionnaireUserInfo',
        component: QuestionnaireUserInfo
    }, {
        path: '/gzdc',
        component: QuestionnaireInfo
    }],
    mode: 'history'
})



